export class ColorMap {
    constructor() {
        this.colors = new Map();
        this.colors.set("LR", "#CC6666");
        this.colors.set("R",  "#FF0000");
        this.colors.set("DR", "#990000");
        this.colors.set("O",  "#FF6600");
        this.colors.set("DO", "#CC6600");
        this.colors.set("LY", "#FFFF99");
        this.colors.set("Y",  "#FFFF00");
        this.colors.set("DY", "#CC9900");
        this.colors.set("LG", "#99CC99");
        this.colors.set("G",  "#009900");
        this.colors.set("DG", "#003300");
        this.colors.set("LB", "#00FFFF");
        this.colors.set("B",  "#3399CC");
        this.colors.set("DB", "#330099");
        this.colors.set("LP", "#CC99CC");
        this.colors.set("P",  "#9900FF");
        this.colors.set("DP", "#330033");
        this.colors.set("W",  "#FFFFFF");
        this.colors.set("LN", "#CCCCCC");
        this.colors.set("N",  "#999999");
        this.colors.set("DN", "#333333");
        this.colors.set("K",  "#000000");
        this.colors.set("LT", "#999966");
        this.colors.set("T",  "#663300");
        this.colors.set("DT", "#330000");
    }

    colorForName(name) {
        return this.colors.get(name);
    }

    get length() {
        return this.colors.size;
    }

    nameForIndex(searchIndex) {
        let currIndex = 0;
        for (const [key] of this.colors) {
            if (searchIndex === currIndex) {
                return key;
            }
            else {
                currIndex++;
            }
        }
        throw `no name for index ${searchIndex}`;
    }
}



